/* NEW STYLES */
html,
body {
  overflow-x: hidden;
}

body {
  min-width: 320px;
}

.catalog_section {
  padding: 70px 0 40px;
}

.wrapper {
  width: 1170px;
  max-width: 96%;
  margin: 0 auto;

}

.h_lg {
