  color: #191919;
  margin-bottom: 60px;
}

.h_md {
  font-family: GothamProMedium, sans-serif;
  font-size: 24px;
  color: #191919;
  margin-bottom: 30px;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  position: relative;
  margin: 0 -10px;
  &:before,
  &:after {
    content: "";
    display: inline-block;
    height: 1px;
  }
}

.grid:before {
  order: 2;
}

.item {
  display: block;
  text-decoration: none;
  margin-bottom: 30px;

}

.item,
.grid:before,
.grid:after {
  margin-left: 10px;
  margin-right: 10px;
}

.grid {
  .item,
  &:before,
  &:after {
    width: 200px;
  }
}

.grid.grid_wide {
  margin: 0 -15px;
  .item,
  &:before,
  &:after {
    width: 270px;
  }
}

.item_img_wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #ededed;
  height: 200px;
  margin-bottom: 20px;
  
  overflow: hidden;
}

.grid_wide .item_img_wrap {
  height: 270px;
}



.item_img_wrap img {
  display: block;
  max-width: 100%;
  max-height: 100%;
  margin: 0 auto;
  transition: all .4s;
}

.item:hover {
