.check_wrap {
  white-space: nowrap;
}

.aside {
  label {
    display: inline-block;
    font-size: 12px;
    margin-right: 15px;
    margin-bottom: 10px;
    cursor: pointer;
    &:last-child {
      margin-right: 0;
    }
    &:before {
      content: "";
      display: inline-block;
      width: 16px;
      height: 16px;
      vertical-align: middle;
      border: 1px solid #c1c1c1;
      background-color: #fdfdfd;
      box-shadow: inset 0 0 0 2px #fdfdfd;
      margin-right: 10px;
      margin-bottom: 3px;
      transition: all .4s;
    }
  }
}

.aside input[type="checkbox"],
.aside input[type="radio"] {
  display: none;
}

.aside input[type="checkbox"]:checked + label:before,
.aside input[type="radio"]:checked + label:before {
