}

.apply_btn {
  display: inline-block;
  min-width: 108px;
  height: 44px;
  line-height: 42px;
  outline: none;
  border: 1px solid #FF2828;
  padding: 0 5px;
  font-family: "GothamProBold";
  font-size: 14px;
  letter-spacing: 1px;
  background-color: #FF2828;
  color: #fff;
  margin-bottom: 10px;
  transition: all .4s;
  cursor: pointer;
  &:hover {
    color: #FF2828;
    background-color: #fff;
  }
}

.accordion {
  font-size: 0;
  cursor: pointer;
  >li {
    position: relative;
    font-family: "GothamProBold";
    font-size: 14px;
    >a,
    >span {
      display: block;
      position: relative;
      height: 50px;
      line-height: 50px;
      padding: 0 32px 0 15px;
      white-space: nowrap;
      border: 1px solid #ececec;
      border-top: none;
      transition: all .4s;
      &:after {
        content: "";
        display: block;
        width: 9px;
        height: 6px;
        position: absolute;
        top: 50%;
        right: 15px;
        transform: translateY(-50%);
        background-image:url(../img/arr1.png);
        background-size: contain;
        background-repeat: no-repeat;
        transition: all .4s;
      }
      &:hover {
        background-color: #364657;
        color: #fff;
        &:after {
          background-image:url(../img/arr2.png);
        }
      }
    }
    a,
    span {
      display: block;
      color: inherit;
      text-decoration: none;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    &.active {
      
      >a,
      >span {
        background-color: #364657;
        color: #fff;
        &:after {
          background-image:url(../img/arr2.png);
        }
      }
    }
    &.active {
      >a,
      >span {
        &:after {
          transform: rotate(-180deg);
        }
      }
    }
    &:first-child {
      >span,
      >a {
        border-top: 1px solid #ececec;
      }
    }
  }
  ul {
    display: none;
    border-bottom: 1px solid #ececec;
    li {
      a,
      span {
        font-family: "GothamProRegular";
        color: #191919;
        height: 46px;
        line-height: 46px;
        background-color: #fdfdfd;
        padding: 0 8px 0 25px;
        transition: all .4s;
        &:hover {
          color: #fff;
          background-color: #6D7D8E;
        }
      }
    }
  }
}

