    margin-left: auto;
    margin-right: auto;
  }
}

@media only screen and (max-width : 600px) {
  .filters_content {
    flex-wrap: wrap;
  }
  .filters_block {
    width: 100%;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .accordion > li {
    width: 49.5%;
  }
}

@media only screen and (max-width : 500px) {
  .accordion > li {
    width: 100%;
  }
}

@media only screen and (max-width : 479px) {
  .product_outer {
    max-width: 100%;
  }
  .sliders_cont {
    flex-wrap: wrap;
    justify-content: center;
  }
  .sm_slider_cont {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 96vw;
    padding-top: 12px;
    margin-right: 0;
    overflow: hidden;
    order: 2;
  }
  .sm_slider {
    width: 220px;
    margin-left: 20px;
    margin-right: 20px;
  }
  .sm_slider .slick-slide {

    margin: 0 4px;
  }
  .slider_arr {
    width: 20px;
    height: 20px;
  }
  .slider_arr_prev {
    transform: rotate(90deg);
  }
  .slider_arr_next {
    transform: rotate(-90deg);
  }
}
